import React from 'react';
import PropTypes from 'prop-types';
import { Formik } from 'formik';

/* Components */
import Task from 'components/Task';

const propTypes = {
	profile: PropTypes.object,
	items: PropTypes.array,
	updateProfile: PropTypes.func,
	isActiveForm: PropTypes.bool,
	toggleActiveForm: PropTypes.func,
};

const ProfileComponent = ({
	profile,
	items,
	updateProfile,
	isActiveForm,
	toggleActiveForm,
}) => (
	<div className="container">
		<h2>Задачи {profile.name}</h2>
		<div className="row">
			<div className="col-md-4">
				<p>
					{profile.name}
				</p>
				<p>
					{profile.phone}
				</p>
				<p>
					{profile.email}
				</p>
				<p>
					{profile.website}
				</p>
				<p>
					<button
						type="button"
						className="btn btn-primary"
						onClick={toggleActiveForm}
						disabled={isActiveForm}
					>
						Редактировать
					</button>
				</p>

				<Formik
					initialValues={{
						name: profile.name || '',
						phone: profile.phone || '',
						email: profile.email || '',
						website: profile.website || '',
					}}
					onSubmit={(values) => {
						updateProfile({ ...values, id: 1 });
						toggleActiveForm();
					}}
					enableReinitialize
					render={props => (
						<form onSubmit={props.handleSubmit}>
							<div className="form-group">
								<label htmlFor="name">Имя</label>
								<input 
									type="text"
									className="form-control"
									name="name"
									id="name"
									placeholder="user.name"
									onChange={props.handleChange}
									onBlur={props.handleBlur}
									value={props.values.name}
									disabled={!isActiveForm}
								/>
							</div>
							<div className="form-group">
								<label htmlFor="phone">Телефон</label>
								<input
									type="text"
									className="form-control"
									name="phone"
									id="phone"
									placeholder="user.phone"
									onChange={props.handleChange}
									onBlur={props.handleBlur}
									value={props.values.phone}
									disabled={!isActiveForm}
								/>
							</div>
							<div className="form-group">
								<label htmlFor="email">Email</label>
								<input 
									type="email" 
									className="form-control" 
									name="email" 
									id="email" 
									placeholder="user.email"
									onChange={props.handleChange}
									onBlur={props.handleBlur}
									value={props.values.email}
									disabled={!isActiveForm}
								/>
							</div>
							<div className="form-group">
								<label htmlFor="website">Сайт</label>
								<input 
									type="text" 
									className="form-control" 
									name="website" 
									id="website" 
									placeholder="user.website"
									onChange={props.handleChange}
									onBlur={props.handleBlur}
									value={props.values.website}
									disabled={!isActiveForm}
								/>
							</div>
							<button
								type="submit"
								className="btn btn-primary"
								disabled={!isActiveForm}
							>
								Сохранить
							</button>
						</form>
					)}
				/>
			</div>
			<div className="col-md-8">
				<ul className="list-group">
					{items.length !== 0 && items.map(task => (
						<Task key={task.id} {...task} userName={profile.name} />
					))}
				</ul>
			</div>
		</div>
	</div>
);

ProfileComponent.propTypes = propTypes;

export default ProfileComponent;