import { connect } from 'react-redux';
import { 
  compose,
  lifecycle,
  withStateHandlers,
} from 'recompose';

/* Actions */
import { actions } from 'views/ducks';

/* Components */
import ProfileComponent from './ProfileComponent';

const mapStateToProps = ({ app: { profile, userTasks } }) => ({ 
  profile,
  items: userTasks,
});

const mapDispatchToProps = dispatch => ({
  getTasks: () => dispatch(actions.getTasks({ id: 1 })),
  updateProfile: values => dispatch(actions.updateProfile(values)),
});

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  lifecycle({
    componentDidMount() {
      const { getTasks, items } = this.props;

      // Если window.__DATA__.tasks пуст, то подгружаем данные с API
      if (items.length === 0) {
        getTasks();
      }
    },
  }),
  withStateHandlers(
    () => ({ isActiveForm: false }),
    {
      toggleActiveForm: ({ isActiveForm }) => () => ({ isActiveForm: !isActiveForm }),
    },
  ),
)(ProfileComponent);